<template>
  <div class="container">
    <h1>NOTAS</h1>
    <b-alert
      :show="dismissCountDown"
      fade
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{ mensaje.texto }}
    </b-alert>

    <form @submit.prevent="agregarNota" v-if="!editar">
      <h3>Agregar una nota</h3>
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.nombre"/>
      <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="nota.descripcion"/>
      <b-button class="btn-success my-2 w-100" type="submit">Agregar nota</b-button>
    </form>

    <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
      <h3>Editar una nota</h3>
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="notaEditar.nombre"/>
      <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="notaEditar.descripcion"/>
      <b-button class="btn-warning my-2 w-100" type="submit">Editar nota</b-button>
      <b-button class="btn my-2 w-100" type="submit" @click="editar=false">Cancelar nota</b-button>
    </form>


    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nombre</th>
          <th scope="col">Descripcion</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          <th scope="row">{{item._id}}</th>
          <td>{{ item.nombre }}</td>
          <td>{{ item.descripcion }}</td>
          <td><b-button @click="eliminarNota(item._id)" class="btn-sm btn-danger">Eliminar</b-button></td>
          <td><b-button @click="activarEdicion(item._id)" class="btn-sm btn-warning">Editar</b-button></td>
        </tr>
      </tbody>
    </table>

  </div>
</template>

<script>
export default {
  data() {
   return {
     notas: [],
     dismissSecs: 5,
     dismissCountDown: 0,
     mensaje:{
       color:'',
       texto:''
     },
     nota: {
       nombre:'',
       descripcion:''
     },
     editar:false,
     notaEditar: {}
   }
  },
  created(){
    this.listarNotas();
  },
  methods: {
    listarNotas() {
      this.axios.get('/notas')
        .then((res) => {
        this.notas = res.data;
      })
      .catch(e => {
        console.log(e.response);
      });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
    agregarNota() {
      this.axios.post('/notas/nueva-nota', this.nota)
      .then((res) => {
        this.notas.push(res.data)
        this.mensaje.color = 'success';
        this.mensaje.texto = 'Nota agregada';
        this.showAlert();
      })
      .catch(e=>{
        console.log(e.response);
        this.mensaje.color = 'danger';
        this.mensaje.texto = e.response.data.msg;
        this.showAlert();
      });
      this.nota.nombre = '';
      this.nota.descripcion = '';
    },
    eliminarNota(id) {
      this.axios.delete(`/notas/${id}`)
      .then(res => {
        const index = this.notas.findIndex( item => item._id === res.data._id)
        this.notas.splice(index,1);
        this.mensaje.color = 'success';
        this.mensaje.texto = 'Nota Eliminada';
        this.showAlert();
      })
      .catch(e => {
        console.log(e);
      })
    },
    activarEdicion(id) {
      this.editar = true;
      this.axios.get(`/notas/${id}`)
      .then((res) => {

        this.notaEditar = res.data;
        console.log(this.notaEditar);
      })
      .catch(e => {
        console.log(e);
      })
    },
    editarNota(item){
      console.log('ITEM:', item);
      this.axios.put(`/notas/${item._id}`,item)
      .then(res => {
        const index = this.notas.findIndex(nota => nota._id === res.data._id);
        console.log(index);
        this.notas[index].nombre = res.data.nombre;
        this.notas[index].descripcion = res.data.descripcion;
        this.mensaje.color = 'success';
        this.mensaje.texto = 'Nota Modificada';
        this.showAlert();
        this.editar = false;
      })
      .catch(e => {
        console.log(e);
      })
    }
  }
};
</script>